

/* BODY */

body{
    font-family:'Cormorant Garamond';
    font-size: 20px;
    //background-color: #f1f0f0;
    margin: 5vw;
   
    
}

input{
    border: solid gray;
    border-radius: 3px;
}

.publish_artwork{
    
   text-align: center;
   text-decoration: underline 8% #FFDE59;
   text-underline-position: under ;
   
}

.artwork{
    position: absolute;
    display: inline-grid;
    top:20vw;
    
    .artwork_title{
        padding-bottom: 5px;
            
    }
    .artwork_champ{
        padding: 2% 10%;
    }

}
.art_form{
    position: relative;
    display: inline-grid;
    top: 15vw;

    .art_form_title{
        padding-bottom: 5%;
    }
    .art_form_category{
        font-family: 'Cormorant Garamond';
        padding: 2% 10%;
        border-radius: 3px;
    }
}
.dimension{
    position: absolute;
    top: 35vw;

    .dimension_title{
        padding-bottom: 5px;
        position: absolute;
        top: 8vw;
       
    }
    .dimension_length{
        position: relative;
        display: grid;
        left: 100%;
        
        
    }
    input{
        
    }
}
.artwork_pic{
    position: relative;
    top: 3vw;
    left: 65%;

}
.description_artwok{
    position: absolute;
    top:40em;
    display: inline-grid;
    

    .description_artwok_title{
        margin:5px;
    }
    .description_artwok_cadre{
        margin: 0 8vw 0 1vw;
        padding: 8vw 25vw;
        top: 3vw;
        left: 1vw;
        position: absolute;
        border: solid gray;
        border-radius: 5px;
    }
}

.save{
    
    color: black;
    background-color: #FFDE59;
    text-decoration: none;
    top: 85vw;
    position: absolute;
    left: 20vw;
    padding: 5px 20px;
    border-radius: 5px;

}
.delete{
    color: black;
    background-color: #FFDE59;
    text-decoration: none;
    top: 85vw;
    position: absolute;
    left: 55vw;
    padding: 5px 20px;
    border-radius: 5px;
}

    



